.userName-main {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin-bottom: 2rem;
    padding-top: 3.5rem;
    padding-left: 8.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.5rem;
    z-index: -2;
}

.userName-main-chat,
.userName-main-profile,
.userName-main-call,
.userName-main-team {
    position: absolute;
    height: 100%;
    top: 5rem;
    left: 9rem;
    right: 1rem;
    bottom: 1rem;
    padding: 2rem 1rem;
    background-color: #f5f5f5;
}

/* profile */
.userName-main-profile-form {
    background-color: #e0e0e0;
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    padding-top: 5rem;
    overflow: hidden;
    padding-bottom: 2rem;
}

.userName-main-profile-form input {
    margin: 3rem;
    margin-top: 0rem;
    width: 60%;
    padding: 0.8rem 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.userName-main-profile-form button {
    margin: 0 auto;
    height: 3rem;
    font-size: 1.4rem;
    font-weight: 600;
    background-color: #5458af;
    color: #fff;
    width: 10rem;
    border-radius: 2rem;
}

.userName-main-profile-form button:hover {
    cursor: pointer;
}

.userName-main-profile-form h3 {
    margin-left: 3rem;
    margin-bottom: 1rem;
    color: #5458af;
}

/* call */

.userName-main-call {
    display: flex;
    flex-direction: column;
}

.userName-main-call-videoContainer {
    display: flex;
    flex-direction: column;
    background-color: #e0e0e0;
    padding: 2rem;
    margin-bottom: 2rem;
    height: 100%;
}

.userName-main-call button {
    width: 6rem;
    height: 2.5rem;
    background-color: #5458af;
    color: #fff;
    border-radius: 10%;
}

.userName-main-call button:hover {
    cursor: pointer;
}

.userName-main-call video {
    margin: 1rem;
    border: solid 2px black;
    box-shadow: 5px 5px 10px;
}

.userName-main-call-input {
    display: flex;
    flex-direction: column;
    margin: 0 10rem;
    margin-top: 10rem;
    margin-bottom: 5rem;
    height: 100%;
}

.userName-main-call-input input {
    width: 100%;
    height: 2rem;
    margin-bottom: 1rem;
    text-align: center;
}

.userName-main-call-input div {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}

.userName-main-call-input div button {
    margin: 1rem;
}

.userName-main-call-videos {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.userName-main-call-answer {
    margin: auto;
    margin-top: 2rem;
}

.userName-main-call-myvideo,
.userName-main-call-uservideo {
    border: solid black 3px;
    padding: 2rem;
    display: flex;
    flex-direction: column;
}

.userName-main-call-myvideo-control,
.userName-main-call-uservideo-control {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.userName-main-call-myvideo-control button,
.userName-main-call-uservideo-control button {
    width: 5rem;
    height: 2rem;
    background-color: #5458af;
    color: #fff;
    border-radius: 10%;
}

/* Teams */
.userName-main-team {
    display: flex;
    flex-direction: column;
}

.userName-main-team-videoContainer {
    display: flex;
    flex-direction: column;
    background-color: #e0e0e0;
    padding: 2rem;
    margin-bottom: 2rem;
    height: 100%;
    overflow: scroll;
}

.userName-main-team-videoContainer input {
    margin: 2rem;
    width: 60%;
    padding: 0.8rem 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.userName-main-team button {
    width: 6rem;
    height: 2.5rem;
    background-color: #5458af;
    color: #fff;
    border-radius: 10%;
}

.userName-main-team button:hover {
    cursor: pointer;
}

.userName-main-team video {
    margin: 1rem;
    border: solid 2px black;
    box-shadow: 5px 5px 10px;
}

.userName-main-team-input {
    margin: auto;
    margin-bottom: 5rem;
    height: 1.5rem;
}

.userName-main-team-input input {
    width: 100%;
    height: 2rem;
    margin-bottom: 1rem;
    text-align: center;
}

.userName-main-team-videos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.userName-main-team-answer {
    margin: auto;
    margin-top: 2rem;
}

.userName-main-team-myvideo,
.userName-main-team-uservideo {
    border: solid black 3px;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.userName-main-team-myvideo-btn button {
    width: 3rem;
    height: 2rem;
    background-color: #5458af;
    color: #fff;
    border-radius: 10%;
}

/* chat */

.userName-main-chat {
    display: flex;
    flex-direction: column;
}

.userName-main-chat-roomForm {
    width: 80%;
    margin: 0 auto;
}

.userName-main-chat-roomForm input {
    margin-top: 12rem;
    width: 100%;
    height: 2rem;
    text-align: center;
}

.userName-main-chat-roomForm button {
    width: 10rem;
    margin: 2rem 24rem;
    height: 2.5rem;
    background-color: #5458af;
    color: #fff;
    border-radius: 10%;
}

.userName-main-chat-roomForm button:hover {
    cursor: pointer;
}

.userName-main-chat-room {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 80%;
    margin: 0 auto;
}

.userName-main-chat-header {
    background-color: #5458af;
    padding: 0rem 2rem;
    color: #fff;
}

.userName-main-chat-body {
    background-color: #fff;
    border-left: 2px solid black;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
    padding: 0rem;
    display: flex;
    flex-direction: row;
    height: 65%;
}

.userName-main-chat-mainside {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.userName-main-chat-form {
    display: flex;
    flex-direction: row;
    background-color: #5458af;
    border-top: 2px solid black;
    padding: 1rem 1.5rem;
    margin: 0;
    justify-content: space-between;
}

.userName-main-chat-form input {
    width: 90%;
    height: 2rem;
}

.userName-main-chat-form-btn {
    width: 5%;
    height: 2.3rem;
    width: 4.8rem;
    background-color: #d9e2f5;
    border: none;
    color: #fff;
    border-radius: 5px;
}

.userName-main-chat-form-btn img {
    display: flex;
    margin: 0 auto;
    justify-content: center;
}

.userName-main-chat-form-btn:hover {
    cursor: pointer;
}

.userName-main-chat-messages {
    height: 100%;
}

.messages {
    padding: 5% 0;
    overflow: auto;
    flex: auto;
}

.messageBox {
    background: #f3f3f3;
    border-radius: 20px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;
}

.messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1em;
    word-wrap: break-word;
}

.messageText img {
    vertical-align: middle;
}

.messageContainer {
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
}

.sentText {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
}

.pl-10 {
    padding-left: 5px;
}

.pr-10 {
    padding-right: 5px;
}

.justifyStart {
    justify-content: flex-start;
}

.justifyEnd {
    justify-content: flex-end;
}

.colorWhite {
    color: white;
}

.colorDark {
    color: #353535;
}

.backgroundBlue {
    background: #2979ff;
}

.backgroundLight {
    background: #f3f3f3;
}
